.about-container {
  margin-top: 6%;
  background-color: white;
  font-size: 16px;
}

.image-container {
  background-image: url('https://images.unsplash.com/photo-1499566727020-881da110a0b0?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=1713&q=80');
  height: 300px;
  background-position: center center;
}

.about-title {
  font-size: 3.5em;
  color: white;
  text-transform: uppercase;
  text-align: center;
  padding-top: 1.8em;
  letter-spacing: 5px;
}

.about-list {
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.about-list li {
  margin-left: 4em;
}

.line {
  margin-top: 1.7em;
  border: 0.5px solid #e8eaed;
}
